export default {
    namespaced :true, //命名空间
    state:{
        lists:[
            // {
            //     isSelect:true,
            //     name:"华为(HUAWEI) p30",
            //     des:"超大屏幕 128G内存 新品上线",
            //     price:3000,
            //     num:1,
            //     imgarr:require('../assets/images/huawei.jpg')
            // },
            //  {
            //     isSelect:true,
            //     name:"爱马仕背包",
            //     des:"轻小,容量大，颜色黑",
            //     price:300,
            //     num:1,
            //     imgarr:require('../assets/images/seckill_1.jpg'),
            // },
            //  {
            //     isSelect:true,
            //     name:"貂皮毛衣",
            //     des:"颜色纯黑，保暖，耐看",
            //     price:199,
            //     num:1,
            //     imgarr:require('../assets/images/nv-fy.jpg'),
            // },
          
        ], //购物车数据列表
        num:0 , //商品的数量
        collist:[]

    },
    mutations:{
        add(state,obj){
            // 把数据添加到购物车列表
            // 判断是否数据是否存在列表中，存在就加一，不存在就添加到列表
            let index = state.lists.findIndex(val=> val.id == obj.id);
            if(index != "-1"){
                state.lists[index].num += 1
                state.num =  state.lists[index].num
            }else{
                obj.isSelect = true;
                state.lists.push(obj)
                state.num = 1
            }
        },
        edit(state,obj){
            Object.assign(state.lists,obj);
            state.num=0
        },
        collect(state,obj){
            // state.collist.push(obj)
            console.log(state.collist)
            let index = state.collist.findIndex(val=> val.id == obj.id);
            if(index != "-1"){
               return
            }else{
                state.collist.push(obj)
            }
        },
        coledit(state,index){
            state.collist.splice(index,1)
        },
        coldel(state){
          
            state.collist=""
        }
    },
    actions:{

    }
}