export default  [{
    path:'/guidepage',
    name:"引导页",
    component:()=>import("../../components/public/guidepage.vue")
  },{
    path:"/advertpage",
    name:"广告页",
    component:()=>import("../../components/public/advertpage.vue"),

    // 独享路由
    beforeEnter:(to,from,next)=>{
      console.log(to,from)
      if(localStorage.advertpage){
        next()
      }else{
        localStorage.advertpage = true
        next("/guidepage")
      }
    }
  },
  {
    path:"/foryou",
    name:"推荐",
    component:()=>import("../../components/public/foryou.vue"),
  },
  {
    path:"/ticket",
    name:"机票",
    component:()=>import("../../components/public/ticket.vue"),
  }, 
  {
    path:"/botnav",
    name:"底部导航",
    component:()=>import("../../components/public/botnav.vue"),
  }
]